/* ===== Header Styling ===== */
.cardHeader {
    display: flex;
    justify-content: space-between;
    background-color: transparent;
    width: 100%;
    padding: 0;
    height: 10%;
}

.leftSection {
    display: flex;
    gap: 16px;
    align-items: flex-end;
    background-color: transparent;
    padding: 12px;
    border-radius: 10px 0 0 10px;
    margin-left: 0.7rem;
}

.inputGroup {
    display: flex;
    flex-direction: column;
    align-items: start;
    font-size: 12px;
    font-weight: bold;
    width: 15dvw;
}

.inputGroup input[type="date"] {
    padding: 6px 12px;
    border: 0px solid black;
    border-radius: 5px;
    font-size: 14px;
    background-color: white;
    color: var(--header-color-hover);
}

.switchGroup {
    display: flex;
    flex-direction: column;
    align-items: start;
    font-size: 14px;
    font-weight: bold;
}

.switch {
    position: relative;
    display: inline-block;
    width: 68px;
    height: 34px;
    margin-right: 10px;
}

.switch input {
    opacity: 0;
    width: 0;
    height: 0;
}

.slider {
    position: absolute;
    cursor: pointer;
    background-color: #ccc;
    border-radius: 34px;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    transition: 0.4s;
}

.slider:before {
    position: absolute;
    content: "";
    height: 26px;
    width: 26px;
    left: 4px;
    bottom: 4px;
    background-color: white;
    transition: 0.4s;
    border-radius: 50%;
}

.switch input:checked+.slider {
    background-color: var(--header-color);
}

.switch input:checked+.slider:before {
    transform: translateX(33px);
}

.autoScroll {
    display: flex;
    background-color: var(--card-color-hover);
    padding: 0.5rem 1rem;
    border-radius: 8px;
    border: 2px solid var(--header-color);
    box-shadow: -2px 2px 4px rgba(0, 0, 0, 0.25);
}

.checkAuto {
    display: flex;
    align-items: center;
    gap: 0.5rem;
    cursor: pointer;
    font-size: 1rem;
    font-weight: 500;
}

.checkAuto input[type="checkbox"] {
    width: 1.2rem;
    height: 1.2rem;
    cursor: pointer;
    accent-color: var(--header-color);
}

.rightSection {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: var(--header-color);
    color: var(--secondary-color);
    padding: 1rem 2rem;
    border-radius: 0 12px 0 0;
    font-weight: bold;
    font-size: 20px;
    top: 0;
}

.buttonFooter {
    position: relative;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    padding: 0.7rem 0.9rem;
    border-radius: 12px;
    background-color: var(--header-color);
    color: #ffffff;
    font-size: 1.8rem;
    cursor: pointer;
    transition: all 0.3s ease;
    border: none;
}

.buttonFooter:hover {
    background-color: var(--header-color-hover);
    color: #ffffff;
    transform: translateY(-2px);
}

.buttonFooter:disabled {
    background-color: #cccccc;
    color: #666666;
    cursor: not-allowed;
    opacity: 0.7;
    transform: none;
    pointer-events: none;
}

.buttonFooter:disabled:hover {
    background-color: #cccccc;
    color: #666666;
}

/* ===== End Header Styling ===== */





/* ===== Body Styling ===== */
/* CARD */
.cardWrapperContainer {
    flex: 1;
    display: flex;
    overflow: hidden;
    padding: 1rem;
}

.cardContainer {
    width: 100%;
    height: 100%;
    overflow: hidden;
    background: var(--card-color-hover);
    border-radius: 1rem;
}

.scrollContentWrapper {
    width: 100%;
    display: flex;
    flex-direction: column;
    animation: scrollachievement 25s linear infinite;
}

.scrollContentWrapperStop {
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    overflow: hidden;
}

.scrollContent {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 1rem;
    padding: 0.5rem 0;
}

.scrollContentStop {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 1rem;
    padding: 1rem;
    overflow: auto;
}

@keyframes scrollachievement {
    0% {
        transform: translateY(0);
    }

    100% {
        transform: translateY(-50%);
    }
}


.cardItem1 {
    border-radius: 12px;
    background-color: var(--header-color);
    box-shadow: -4px 4px 8px rgba(80, 80, 80);
    transition: all 0.3s ease;
    cursor: pointer;
    display: flex;
    flex-direction: column;
}

.cardItem1:hover {
    transform: scale(1.01);
    box-shadow: -8px 8px 16px rgba(80, 80, 80);
    cursor: pointer;
}

.cardItem1Header {
    display: flex;
    width: 100%;
}

.cardTitle {
    display: flex;
    align-items: start;
    justify-content: start;
    background-color: var(--secondary-color);
    color: var(--header-color);
    padding: 0.25rem 3rem;
    border-radius: 12px 0;
    font-size: 1.25rem;
    font-weight: bold;
}

.cardItem1Body {
    flex: 1;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    padding: 0.75rem;
    gap: 0.75rem;
}

.cardSubItem {
    display: flex;
    background-color: var(--header-color);
    border-radius: 8px;
    box-shadow: -1px 1px 2px rgba(0, 0, 0, 0.5);
    flex-direction: column;
}

.cardSubItemHeader {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    background-color: var(--secondary-color);
    border-radius: 8px 8px 0 0;
    font-size: 1rem;
    font-weight: 500;
    padding: 0.10rem 0;
    color: var(--header-color);
}

.cardSubItemBody {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    flex: 1;
    width: 100%;
    background-color: var(--header-color);
    border-radius: 0 0 8px 8px;
    padding: 0.25rem;
    gap: 0.25rem;
}

.cardSubItemFooter {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 0.75rem;
    padding: 0.15rem 0.5rem;
    color: var(--header-color);
    background-color: var(--secondary-color);
    border-radius: 0 0 12px 12px;
}

.cardSubItemFooter>div:first-child {
    display: flex;
    align-items: center;
    justify-content: center;
}

.cardSubItemFooter>div:last-child {
    font-weight: 500;
    color: white;
    background-color: var(--header-color);
    padding: 0 0.5rem;
    border-radius: 4px;
    font-size: 14px;
}

.itemValue {
    background-color: var(--header-color);
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    box-shadow: -2px 2px 4px rgba(0, 0, 0, 0.5);
    border-radius: 8px;
    padding: 8px 0;
}

.itemValue h2 {
    color: var(--secondary-color);
    font-size: 1rem;
}

.itemValue h3 {
    color: white;
    font-size: 0.75rem;
    font-weight: 400;
}

.itemNormal {
    background-color: var(--connect-color);
}

.itemWarning {
    background-color: var(--warning-color);
}

.itemAlarm {
    background-color: var(--disconnect-color);
}



.cardItem1Footer {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    padding: 0.25rem 1rem;
    color: var(--header-color);
    background-color: var(--secondary-color);
    border-radius: 0 0 12px 12px;
}

.cardItem1Footer>div:first-child {
    font-weight: 600;
}

.cardItem1Footer>div:last-child {
    font-weight: bold;
    color: white;
    background-color: var(--header-color);
    padding: 0.1rem 1rem;
    border-radius: 4px;
}





.cardItem {
    background-size: cover;
    background-position: center;
    border-radius: 16px;
    overflow: hidden;
    color: white;
    display: flex;
    flex-direction: column;
    box-shadow: -4px 4px 8px rgba(80, 80, 80);
    position: relative;
    transition: all 0.3s ease;
    cursor: pointer;
}

.cardItem::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: inherit;
    background-size: cover;
    background-position: center;
    filter: blur(2px);
    z-index: 1;
}

.cardItem>* {
    position: relative;
    z-index: 2;
}

.cardItem:hover {
    transform: scale(1.01);
    box-shadow: -8px 8px 16px rgba(80, 80, 80);
    cursor: pointer;
}

.cardItemHeader {
    display: flex;
    height: auto;
    width: 100%;
}

.cardItemTitle {
    display: flex;
    padding: 0.5rem 2rem;
    background-color: var(--header-color);
    border-radius: 1rem 0;
    color: white;
    font-size: 1.2rem;
    font-weight: bold;
}

.cardItemBody {
    width: 100%;
    flex: 1;
    box-sizing: border-box;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    align-items: stretch;
    padding: 0.5rem;
    gap: 0.5rem;
}



