.runningBackground {
    background-color: var(--header-color);
}

@keyframes blinkWarning {
    0% {
        background-color: var(--warning-color);
    }

    55% {
        background-color: var(--header-color);
    }

    100% {
        background-color: var(--warning-color);
    }
}

.warningBackground {
    animation:
        blinkWarning 2s infinite alternate ease-in-out;
}

@keyframes blinkDanger {
    0% {
        background-color: var(--disconnect-color);
    }

    55% {
        background-color: var(--header-color);
    }

    100% {
        background-color: var(--disconnect-color);
    }
}

.dangerBackground {
    animation:
        blinkDanger 2s infinite alternate ease-in-out;
}